.task {
  padding: 2.5px 0;
  /* border: 1px solid #000; */
  /* border: 1px solid black; */
}

.taskDiv {
  display: grid;
  grid-template-columns: 1fr 100px;
}
.taskDiv p {
  align-self: center;
  text-align: left;
  word-break: break-all;
  font-size: 16px;
  padding-right: 20px;
  cursor: pointer;
  user-select: none;
  color: rgb(97, 218, 251);
}

.taskHeader {
  position: relative;
  padding-left: 40px;
}

.emojiTicker {
  font-size: 21px;
  width: 40px;
  text-align: center;
  align-self: flex-start;
  position: absolute;
  left: 0;
  top: -6px;
}
.taskHeaderOver h5 {
}
.taskDiv p[data-complete="true"] {
  opacity: 0.2;
  text-decoration: line-through;
}
